import React from 'react'
import styles from './Modal.module.scss'
import classNames from 'classnames'
import { Form } from '../Form/Form'
import { ReactComponent as Cross } from 'src/assets/images/cross.svg'

export const Modal = ({isActive, close}) => {
  return (
    <div className={classNames(styles.modal, isActive ? styles.active : null)} onClick={close}>
      <div className={styles.container} onClick={e => e.stopPropagation()}>
        <div className={styles.row}>
          <div className={styles.col}>
            <div className={styles.title}>
              Обсудить <br/> проект
            </div>
          </div>
          <div className={styles.col}>
            <p className={styles.text}>
              Расскажите нам о своем проекте, подумаем над ним вместе. Начните с простого — просто напишите нам.
            </p>
            <div className={styles.form}>
              <Form />
            </div>
          </div>
        </div>
        <button className={styles.close} onClick={close}>
          <Cross />
        </button>
      </div>
    </div>
  )
}
