import React, { useEffect, useState } from 'react'
import styles from './Showreel.module.scss'
import { ReactComponent as Play } from 'src/assets/images/play.svg'
import { useScroll } from 'framer-motion'

export const Showreel = ({offset, node,}) => {
  const links = [
    {text: 'Bahance', href: '#'},
    {text: 'Презентация', href: '#2'},
  ]
  const [result, setResult] = useState()

  const { scrollYProgress } = useScroll({
    target: node,
    offset: ["-32px start", `${offset} end`]
  })

  useEffect(() => {
    scrollYProgress.onChange(res => {
      setResult(res)
    })
  }, [])

  return (
    <div>
      <div className={styles.row}>
        <div className={styles.col}>
          <ul className={styles.list}>
            {links.map(item => 
              <li className={styles.item} key={item.href}>
                <a className={styles.link} href={item.href}>{item.text}</a>
              </li>
            )}
          </ul>
        </div>
        <div className={styles.col} style={{paddingLeft: `${25.3 * result}%`}}>
          <div className={styles.container}>
            <div className={styles.wrapper}>
              <div className={styles.label}>
                <div className={styles.icon}>
                  <Play />
                </div>
                <div className={styles.text}>Showreel</div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  )
}
