import React, { useRef, useEffect, useState } from 'react'
import { Container } from '../Container/Container'
import { IntroLine } from '../IntroLine/IntroLine'
import { Row } from '../Row/Row'
import { Showreel } from '../Showreel/Showreel'
import styles from './Intro.module.scss'
import { IntroTitle } from './IntroTitle'

export const Intro = () => {

  const [offset, setOffset] = useState('0px')

  const showreelRef = useRef(null)
  const titleRef = useRef(null)

  useEffect(() => {
    if (showreelRef.current) {
      const offset = window.innerHeight - showreelRef.current.offsetTop

      setOffset(`${offset}px`)
    }
  }, [showreelRef])

  const data = ['Retail', 'FMCG', 'E-commerce', 'IT & Telecom', 'Промышленность', ]

  return (
    <div className={styles.intro}>
      <Container>
        <div className={styles.bg}></div>
        <ul className={styles.list}>
          {data.map(item =>
            <li className={styles.item} key={item}>{item}</li>
          )}
        </ul>
        <div className={styles.bot}>
          <Row>
              <IntroTitle>Digital</IntroTitle>
              <div className={styles.col} ref={titleRef}>
                {titleRef.current &&
                  <IntroLine node={titleRef} />}
            </div>
          </Row>
        </div>
        <div ref={showreelRef} className={styles.showreel}>
          {showreelRef.current &&
            <Showreel offset={offset} node={showreelRef}  />}
        </div>
      </Container>
    </div>
  )
}
